h4 {
  padding: 0;
  margin: 0;
  line-height: 1.3;
  font-weight: 600;
  color: white;
  max-width: 260px;
  word-wrap: normal;
}

a,
p {
  padding: 0;
  margin: 0;
  line-height: 1.3;
  font-weight: 600;
  color: rgb(255, 255, 255);
  font-size: 0.7rem;
}

#root {
  margin: auto;
  height: 100%;
  width: 1903px;
}

.header {
  width: 1903px;
  height: 120px;
  position: absolute;
  background-image: url("./banner_placeholder.png");

}

.topNav {
  background: rgba(75, 75, 75, 0.445);
  width: 500px;
  height: 30px;
  position: absolute;
  top: 80px;
  left: 1000px;
  border: .2em solid hsl(0, 0%, 33%);
  box-shadow: 3px 3px .3em -.1em black inset;
}

.sideNav {
  background: rgba(75, 75, 75, 0.445);
  width: 285px;
  height: fit-content;
  position: fixed;
  top: 134px;
  border-radius: .5em;
  border: .2em solid hsl(0, 0%, 33%);
  box-shadow: 3px 3px .3em -.1em black inset;
  /* #282A2B */
}

.button1 {
  width: 228px;
  margin-top: 20px;
  margin-bottom: 10px;
  display: block;
  margin-left: auto;
  margin-right: auto;
  height: 25px;
}

.button3 {
  width: 150px;
  display: block;
  text-align: center;
  margin-top: 4px;
  margin-left: 5px;
  height: 20px;
}

.button1, .button2, .button3 {
  border: 0.1em solid #FFFFFF;
  color:#FFFFFF;
  transition: all 0.2s;
  background-color: #00000069;
}

.button1:hover, .button2:hover, .button3:hover{
  color: #000000;
  background-color: #FFFFFF;
  cursor: pointer;
}

.mainContainer {
  background: rgba(0, 0, 0, 0.473);
  width: 1574px;
  position: relative;
  top: 134px;
  left: 320px;
  height: fit-content;
}


.topContainer {
  background: rgba(0, 0, 0, 0.473);
  width: 100%;
  top: 14%;
  left: 17%;
  display: inline-block;
  height: fit-content;
}

.description {
  width: 99.5%;
  height: fit-content;
  display: inline-block;
  margin-bottom: 5px;
  background: rgba(75, 75, 75, 0.445);
  border-radius: .3em;
  border: .2em solid hsl(0, 0%, 33%);
  box-shadow: 3px 3px .3em -.1em black inset;

  font-size: 1.1rem;
  text-align: justify;
  color: white;
}

.scPanel {
  width: 99.5%;
  height: fit-content;
  display: inline-block;
  margin-bottom: 5px;
  background: rgba(75, 75, 75, 0.445);
  border-radius: .5em;
  border: .3em solid hsl(0, 0%, 33%);
  box-shadow: 3px 3px .3em -.1em black inset;

}

.button2 {
  display: inline-block;
  margin-left: 10px;
  margin-top: 14px;
  margin-bottom: 14px;
  height: 25px;
  width: fit-content;
  text-align: center;
}

.bottomContainer {
  background: rgba(0, 0, 0, 0.473);
  width: 100%;
  top: 14%;
  left: 17%;
  display: inline-block;
  height: fit-content;
}

.scContainer {
  width: 100%;
  height: 724px;
  display: inline-block;
  background: rgba(0, 0, 0, 0.705);
}

.pictureContainer {
  width: 1328px;
  height: 724px;
  display: inline-flex;
  border-radius: .5em;
  border: .3em solid hsl(0, 0%, 33%);
  /* #282A2B */

 
  background: rgba(0, 0, 0, 0.705);
}

.picture {
  margin: auto;
  width: inherit;
  height: inherit;
  object-fit: scale-down;
}

.picture:hover {
  cursor: zoom-in;
}

.sPanel {
  width: 14%;
  height: 724px;
  overflow-y: auto;
  margin-left: 5px;
  border-radius: .5em;
  border: .3em solid hsl(0, 0%, 33%);
  display: inline-block;
  position: absolute;
  background: rgba(75, 75, 75, 0.445);
  box-shadow: 3px 3px .3em -.1em black inset;
}

.sThumbContainer {
  display: block;
  border: 0.1em solid #555555;
  height: 90px;
  width: 190px;
  margin-left: auto;
  margin-right: auto;
  margin-top: 10px;
  background: black;
}

.sThumbContainer:hover {
  border: 0.1em solid #f3f3f3;
  cursor: pointer;
}
  
.sThumb {
  margin: auto;
  width: inherit;
  height: inherit;
  object-fit: scale-down;
}

.zoomedImageContainer {
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  align-items: center;
  display: flex;
  overflow: auto;
  background: black;
  position: fixed;
  z-index: 2;
  visibility: hidden;
}

.zoomedImage {
  display: block;
  margin: auto;
  
  z-index: 3;
}

.zoomedImage:hover {
  cursor: zoom-out;
}


/* width */
::-webkit-scrollbar {
  width: 15px;
}

/* Track */
::-webkit-scrollbar-track {
  background: #2b2b2b;
  border-radius: .4em;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(128, 127, 127);
  border-radius: .4em;
}

/* Handle on hover */
::-webkit-scrollbar-thumb:hover {
  background: rgb(197, 196, 196);
}

::-webkit-scrollbar-corner {
  background: black;
}

.selected {
  color: white;
  margin-top: 5px;
  
  text-align: center;
  border-bottom: .3em solid hsl(0, 0%, 33%);

}